<!--  -->
<template>
  <div class="layout">
    <ul class="tags clearfix">
      <router-link tag="li" to="/Home" class="active">
        网站主页
        <span>></span>
      </router-link>
      <li>编辑推荐</li>
    </ul>
    <div class="content clearfix">
      <!-- 分享 -->
      <div class="shareList">
        <h3>分享</h3>
        <ul>
          <li>
            <img src="@/assets/image/info/WeChatpage.png" alt />
          </li>
        </ul>
      </div>
      <!-- 正文 -->
      <section class="a-section">
        <dl class="header">
          <dt>站酷网</dt>
          <img src="@/assets/image/info/i1.png" alt />
          <dd class="i-tag middle">
            <label>编辑印象：</label>
            <span v-for="item in labelList">{{item}}</span>
          </dd>
          <dd class="star middle">
            <font>编辑指数：</font>
            <span></span>
            <span></span>
            <span></span>
            <span></span>
            <span></span>
          </dd>
          <dd>编辑点评： 一个路由匹配到的所有路由记录会暴露为 $route 对象</dd>
        </dl>

        <div class="printscreen">
          <h3>小程序截图</h3>
          <div class="imgList">
            <img src="@/assets/image/info/xinfo.png" alt />
            <img src="@/assets/image/info/xinfo.png" alt />
            <img src="@/assets/image/info/xinfo.png" alt />
          </div>
        </div>

        <div class="g-info clearfix">
          <div class="left">
            <h3>公众号信息</h3>
            <p class="moreWord">
              运营商：
              <font>北京站酷网有限公司</font>
            </p>
            <p class="moreWord">
              类别：
              <font>设计参考</font>
            </p>
            <p class="moreWord">
              简介：
              <font>一个路由匹配到的所有路由记录会暴露为</font>
            </p>
          </div>
          <div class="right">
            <img src="@/assets/image/info/QRcodepublic.png" alt />
            <p>ID：ZCOOL</p>
          </div>
        </div>
        <!-- 点赞评论 -->
        <v-CommentFavour></v-CommentFavour>
      </section>
      <!-- 推荐 -->
      <div class="recommend">
        <div class="bottom">
          <h3>其他推荐</h3>
          <ul>
            <li class="b-li clearfix" v-for="item in dataBottom">
              <img :src="item.src" alt />
              <article>
                <dt :title="item.title" class="moreMWord">{{item.title}}</dt>
                <dd>编辑点评：{{item.remark}}</dd>
                <dd class="s-star middle" v-html="item.stars"></dd>
              </article>
            </li>
          </ul>
        </div>
        <div class="top">
          <h3>值得读</h3>
          <ul>
            <li class="t-li clearfix" v-for="item in dataTop">
              <img :src="item.src" alt />
              <article>
                <dt :title="item.title" class="moreMWord">{{item.title}}</dt>
                <dd class="count clearfix">
                  <span>{{item.time}}天前</span>
                  <span>{{item.see}}人阅读</span>
                </dd>
              </article>
            </li>
          </ul>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
//这里可以导入其他文件（比如：组件，工具js，第三方插件js，json文件，图片文件等等）
//例如：import 《组件名称》 from '《组件路径》';
import CommentFavour from "@/components/common/plugin/commentFavour.vue";
export default {
  //import引入的组件需要注入到对象中才能使用
  components: {
    "v-CommentFavour": CommentFavour
  },
  data() {
    //这里存放数据
    return {
      labelList: ["电影", "科幻", "幕后特效"],

      dataTop: [
        {
          src: require("@/assets/image/info/article1.png"),
          title:
            "通过在 Vue 根实例的 router 配置传入 router 实例，下面这些属性成员会被注入到每个子组件。",
          time: 3,
          see: 213213
        },
        {
          src: require("@/assets/image/info/article1.png"),
          title:
            "通过在 Vue 根实例的 router 配置传入 router 实例，下面这些属性成员会被注入到每个子组件。",
          time: 3,
          see: 213213
        }
      ],
      dataBottom: [
        {
          src: require("@/assets/image/b3.png"),
          title: "美团",
          remark: "这样你便可以将参数转换成另一种类型，将静态值与基",
          see: 3,
          editStar: 2
        },
        {
          src: require("@/assets/image/b2.png"),
          title: "玩吧",
          remark: "这样你便可以将参数转换成另一种类型，将静态值与基",
          see: 4,
          editStar: 3.5
        }
      ]
    };
  },
  //监听属性 类似于data概念
  computed: {},
  //监控data中的数据变化
  watch: {},
  //方法集合
  methods: {
    orgStar() {
      this.dataBottom.map(function(val) {
        var data = val.editStar;
        let stars = String(data);
        let starsNum = stars.split(".");
        let len = 0;
        let span_star = "",
          span = "",
          halfstar = "";
        if (starsNum.length > 1) {
          len = 4;
          halfstar = '<span class="halfstar"></span>';
        } else {
          len = 5;
        }
        for (let i = 0; i <= len; i++) {
          if (i == starsNum[0]) {
            for (let j = 0; j < len; j++) {
              if (j < starsNum[0]) {
                span_star += '<span class="stars"></span>';
              } else {
                span += "<span></span>";
              }
            }
          }
        }
        let starAll = "<font>编辑指数：</font>" + span_star + halfstar + span;
        val.stars = starAll;
      });
    }
  },
  //生命周期 - 创建完成（可以访问当前this实例）
  created() {
    this.orgStar();
  },
  //生命周期 - 挂载完成（可以访问DOM元素）
  mounted() {},
  beforeCreate() {}, //生命周期 - 创建之前
  beforeMount() {}, //生命周期 - 挂载之前
  beforeUpdate() {}, //生命周期 - 更新之前
  updated() {}, //生命周期 - 更新之后
  beforeDestroy() {}, //生命周期 - 销毁之前
  destroyed() {}, //生命周期 - 销毁完成
  activated() {} //如果页面有keep-alive缓存功能，这个函数会触发
};
</script>
<style lang='scss' scoped>
//@import url(); 引入公共css类
$c9: #999;
$c6: #666;
$c3: #333;
$b: #2878ff;
$f4: #f4f6f8;
.layout {
  width: 1240px;
  margin: 0 auto;
  .tags {
    padding: 30px 0 50px 0;
    li {
      float: left;
      margin-right: 5px;
      color: $c6;
      cursor: pointer;
      font-size: 14px;
      line-height: 14px;
      &.active {
        color: $b;
      }
    }
  }
}
.content {
  .shareList {
    float: left;
    text-align: center;
    margin-top: 100px;
    margin-left: -108px;
    h3 {
      font-size: 14px;
      color: $c9;
      &::before {
        content: "";
        display: inline-block;
        height: 1px;
        width: 24px;
        background: $c9;
        vertical-align: middle;
        margin-right: 4px;
      }
      &::after {
        content: "";
        display: inline-block;
        height: 1px;
        width: 24px;
        background: $c9;
        vertical-align: middle;
        margin-left: 4px;
      }
    }
    ul {
      margin-top: 30px;
      li {
        cursor: pointer;
      }
    }
  }
  section.a-section {
    width: 736px;
    float: left;
    overflow: hidden;
    margin-right: 125px;
    .header {
      dt {
        font-size: 30px;
        color: $c3;
        line-height: 30px;
      }
      img {
        margin: 26px 0 30px 0;
      }
      .i-tag {
        span {
          display: inline-block;
          border: 1px solid $f4;
          color: $c6;
          background: $f4;
          padding: 6px 10px;
          border-radius: 3px;
          font-size: 14px;
          margin-right: 10px;
        }
      }
      // .star {
      //   width: 100%;
      //   font-size: 0;
      //   font {
      //     font-size: 14px;
      //     color: #999;
      //     vertical-align: middle;
      //   }
      //   span {
      //     display: inline-block;
      //     vertical-align: middle;
      //     margin-right: 10px;
      //     width: 15px;
      //     height: 15px;
      //     background: url("../../assets/image/notstar.png") no-repeat;
      //     &.full {
      //       background: url("../../assets/image/stars.png") no-repeat;
      //     }
      //     &.half {
      //       background: url("../../assets/image/halfstar.png") no-repeat;
      //     }
      //   }
      // }
      dd {
        margin-bottom: 30px;
      }
    }
  }

  .printscreen {
    border-bottom: 1px solid #eceff2;
    border-top: 1px solid #eceff2;
    padding-bottom: 40px;
    h3 {
      font-size: 18px;
      color: #333;
      line-height: 18px;
      margin: 40px 0 30px 0;
    }
    .imgList {
      font-size: 0;
      overflow: hidden;
      height: 410px;
      img {
        margin-right: 22px;
        &:last-of-type {
          margin-right: 0;
        }
      }
    }
  }

  .g-info {
    margin-bottom: 70px;
    .left {
      float: left;
      width: 600px;
      overflow: hidden;
      h3 {
        font-size: 18px;
        color: #333;
        line-height: 18px;
        margin: 40px 0 30px 0;
      }
      p {
        margin-bottom: 20px;
        font-size: 14px;
        color: #666;
        &:last-of-type {
          margin-bottom: 0;
        }
        font {
          color: #333;
        }
      }
    }
    .right {
      float: right;
      font-size: 0;
      img {
        width: 106px;
        height: 106px;
        box-sizing: border-box;
        border: 1px solid #eceff2;
        margin: 40px 0 10px 0;
      }
      p {
        font-size: 14px;
        line-height: 14px;
        color: #333;
        text-align: center;
      }
    }
  }

  .recommend {
    float: left;
    width: 375px;
    h3 {
      font-size: 24px;
      line-height: 24px;
      color: #333;
      margin-bottom: 30px;
    }

    .top {
      margin-top: 50px;
      ul {
        .t-li {
          margin-bottom: 30px;
          height: 90px;
          cursor: pointer;
          &:last-of-type {
            margin-bottom: 0;
          }
          img {
            float: left;
          }
          article {
            position: relative;
            margin-left: 144px;
            height: 100%;
            dt {
              font-size: 16px;
              color: #333;
              line-height: 22px;
            }
            .count {
              position: absolute;
              bottom: 0;
              width: 100%;
              span {
                font-size: 14px;
                color: #999;
                line-height: 14px;
                float: left;
                &:nth-of-type(2) {
                  float: right;
                }
              }
            }
          }
        }
      }
    }
    .bottom {
      ul {
        padding-bottom: 50px;
        border-bottom: 1px solid #eceff2;
        .b-li {
          margin-bottom: 36px;
          height: 110px;
          cursor: pointer;
          &:last-of-type {
            margin-bottom: 0;
          }
          img {
            float: left;
            width: 110px;
            height: 110px;
            border: 1px solid #f4f6f8;
            box-sizing: border-box;
            border-radius: 10px;
          }
          article {
            margin-left: 140px;
            position: relative;
            height: 100%;
            dt {
              font-size: 18px;
              color: #333;
              line-height: 18px;
            }
            dd:nth-of-type(1) {
              margin: 12px 0 20px 0;
              font-size: 14px;
              color: #999;
            }
            .star {
              position: absolute;
              bottom: 0;
              width: 100%;
              font-size: 0;
              font {
                font-size: 14px;
                color: #999;
                vertical-align: middle;
              }
              span {
                display: inline-block;
                vertical-align: middle;
                margin-right: 10px;
                width: 15px;
                height: 15px;
                background: url("../../assets/image/notstar.png") no-repeat;
                &.full {
                  background: url("../../assets/image/stars.png") no-repeat;
                }
                &.half {
                  background: url("../../assets/image/halfstar.png") no-repeat;
                }
              }
            }
          }
        }
      }
    }
  }
}
</style>


